import React, { useState, useCallback, useEffect } from 'react'
// import Slider from '@material-ui/lab/Slider'
import Cropper from 'react-easy-crop'
// import './styles.css'
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Button from '@material-ui/core/Button';

import getCroppedImg from './cropImage'
export default ({origImg, setBlob, modal, setModal, setImg}) => {
  const [crop, setCrop] = useState({ x: 0, y: 0 })
  const [initialCroppedAreaPixels, setInitialCroppedAreaPixels] = useState(
    undefined
  )
  const [zoom, setZoom] = useState(1)
  useEffect(() => {
    const croppedAreaPixels = JSON.parse(
      window.localStorage.getItem('croppedAreaPixels')
    )
    setInitialCroppedAreaPixels(croppedAreaPixels)
  }, [])
  
  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {

    window.localStorage.setItem(
        'croppedAreaPixels',
        JSON.stringify(croppedAreaPixels)
      )
    }, [])
  const done = async() => {
    try {
      const croppedImage = await getCroppedImg(
        origImg,
        JSON.parse(window.localStorage.getItem('croppedAreaPixels')),
        0
      )
  
      // setCroppedImage(croppedImage)
      setBlob(croppedImage)
    } catch (e) {
      console.error(e)
    }
    setModal(false)
  }

  return (
    <Dialog fullWidth onClose={()=>setModal(false)} aria-labelledby="simple-dialog-title" open={modal}>
      
      <DialogTitle id="simple-dialog-title">Установить обложку для манги</DialogTitle>  
      <DialogContent >
        <div style={{height:350,position:'relative'}}>
          <div className="crop-container">
            <Cropper
              image={origImg}
              crop={crop}
              zoom={zoom}
              aspect={250 / 350}
              onCropChange={setCrop}
              onCropComplete={onCropComplete}
              onZoomChange={setZoom}
              initialCroppedAreaPixels={initialCroppedAreaPixels}
            />
          </div>
        </div>
      </DialogContent>
      <DialogContent >
        <div style={{marginBottom:12}}>
          <Button onClick={done} style={{marginRight:8}} variant="outlined" size="small" color="secondary">
            Сохранить
          </Button>
          <Button onClick={()=>{
            setBlob(null)
            setImg(null)
            setModal(false)
            }} variant="outlined" size="small">
            Отмена
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

