import React, { useState,useEffect,useCallback } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import Grid from '@material-ui/core/Grid';


import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

import { Link as RRDLink ,Redirect,useHistory } from 'react-router-dom';
import { observer, inject } from 'mobx-react';
import InputLabel from '@material-ui/core/InputLabel';

import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import filterParams from '../filter.params';
import Resizer from '../Components/resizer'
import Autocomplete from '@material-ui/lab/Autocomplete';

import IconButton from '@material-ui/core/IconButton';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import axios from "axios";
const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  chb:{
    marginTop:16, 
    marginBottom:8
  }

}));



const CreateMangaPage = observer(({authStore}) => {
  const classes = useStyles();
  const [param,setParam] = useState({type:'1', status:'1'})
  const [origImg, setOrigImg] = useState(null)
  const [img, setImg] = useState(null)
  const [modal, setModal] = useState(false)
  const history = useHistory();
  const [blob, setBlob] = useState(null)
  const uploadImg = async(blob)=>{
    // console.log('blob',blob)
    let formData = new FormData();
    formData.set('file', blob);
    const res = await axios.post(`${process.env.REACT_APP_API_BASE}/upload/mangaImg`, formData)
    setImg(res.data.url)
  }
  useEffect(()=>{
    if(blob){
      uploadImg(blob)
    }
  },[blob])
  const sendManga = async(p) => {

    p.imgUrl=img
    const res = await axios.post(`${process.env.REACT_APP_API_BASE}/manga`, p)

    history.push("/manga");
  }
  const onFileChange = async e => {
    
    if (e.target.files && e.target.files.length > 0) {

      const file = e.target.files[0]
      function readFile(file) {
        return new Promise(resolve => {
          const reader = new FileReader()
          reader.addEventListener('load', () => resolve(reader.result), false)
          reader.readAsDataURL(file)
        })
      }
      let imageDataUrl = await readFile(file)
      // apply rotation if needed
      setOrigImg(imageDataUrl)
      setModal(true)
    }
  }




  if(!authStore.access_token){
    return <Redirect push to="/login" />
  }

  return (
    <>

      <div className={classes.paper}>
        <Typography component="h1" variant="h5">
          Добавление новой манги
        </Typography>
        <form onSubmit={e=>{
          e.preventDefault()
          const [
            originalTitle,
            title,
            englishTitle,
            otherTitles,
            year,
            description,
            author
          ] = [
            e.target.originalTitle.value,
            e.target.title.value,
            e.target.englishTitle.value,
            e.target.otherTitles.value.split('/'),
            e.target.year.value,
            e.target.description.value,
            e.target.author.value,
          ]
          const p = {originalTitle,title,englishTitle,otherTitles:otherTitles[0]===""?[]:otherTitles,year,description,author,...param}
          sendManga(p)
        }} className={classes.form} noValidate>
          <Resizer origImg={origImg} setBlob={setBlob} setImg={setImg} modal={modal} setModal={setModal}/>
          <div style={{display:'flex'}}>
            <div style={{ backgroundImage:img?`url(${img})`:"",'background-size': 'cover', display: 'flex',justifyContent: 'center', alignItems: 'center',width: 300*(250/350),height:300,margin:14,marginLeft:0, border:'solid 0.1px'}}>
              <div style={{position: 'absolute'}}>
                  {!img&&<>
                    <input value={''} onInput={onFileChange} accept="image/*" style={{display: 'none'}} id="icon-button-file" type="file" />
                    <label htmlFor="icon-button-file">
                    <IconButton color="primary" aria-label="upload picture" component="span">
                      <PhotoCamera />
                    </IconButton>
                    </label>
                  </>
                  }
                
                {img&&<IconButton onClick={()=>{setImg(null)}} aria-label="clear picture" component="span">
                  <HighlightOffIcon />
                </IconButton>
                }
              </div>
            </div>
            <div style={{width:'calc(100% - 220px)'}}>
              <TextField
                variant="outlined"
                margin="normal"
                // required
                fullWidth
                id="originalTitle"
                label="Оригинальное название (без иероглифов)"
                name="originalTitle"
                autoComplete="originalTitle"
                autoFocus
              />
              <TextField
                variant="outlined"
                margin="normal"
                // required
                fullWidth
                id="title"
                label="Русское название"
                name="title"
                autoComplete="title"
                autoFocus
              />
              <TextField
                variant="outlined"
                margin="normal"
                // required
                fullWidth
                id="englishTitle"
                label="Английское название"
                name="englishTitle"
                autoComplete="englishTitle"
                autoFocus
              />
              <TextField
                variant="outlined"
                margin="normal"
                fullWidth
                id="otherTitles"
                label='Альтернативные названия через "/"'
                name="otherTitles"
                autoComplete="otherTitles"
                autoFocus
              />
            </div>
          </div>
          <TextField
            variant="outlined"
            margin="normal"
            // required
            fullWidth
            id="author"
            label="Автор"
            name="author"
            autoComplete="author"
            autoFocus
          />
          <Grid style={{ padding: '8px 0px' }} container spacing={1}>
            <Grid item xs={4}>
              <SelectTypeList value={param.type} setParam={(newParam)=>setParam({...param,type:newParam})}/>
            </Grid>
            <Grid item xs={4}>
              <SelectStatusList value={param.status} setParam={(newParam)=>setParam({...param,status:newParam})}/>
            </Grid>
            <Grid item xs={4}>
              <TextField
                fullWidth
                variant="outlined"
                margin="none"
                // required
                id="year"
                label="Год релиза"
                name="year"
                autoComplete="year"
                autoFocus
              />
            </Grid>
          </Grid>
          <div className={classes.chb}>
            <CheckboxesGenres setParam={(newParam)=>setParam({...param,genre:newParam})}/>
          </div>
          <div className={classes.chb}>
            <CheckboxesFormat setParam={(newParam)=>setParam({...param,format:newParam})}/>
          </div>
          <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="description"
            label='Описание'
            name="description"
            autoComplete="description"
            autoFocus
            multiline
            rows={5}
            rowsMax={5}
          />
          <br></br><br></br>
          <Button type="submit" style={{marginRight:8}} variant="outlined" size="medium" color="secondary">
            Добавить
          </Button>
          <Button onClick={()=>history.push("/manga")}  variant="outlined" size="medium">
            Отмена
          </Button>
        </form>
      </div>

    </>
  );
})

export default inject("authStore")(CreateMangaPage);


// const icon = <CheckBoxOutlineBlankIcon fontSize="small" />;
// const checkedIcon = <CheckBoxIcon fontSize="small" />;

function CheckboxesGenres({setParam}) {
  return <Checkboxes setParam={setParam} options={filterParams.genre} label={'Жанры'} placeholder={'Выберете один или несколько жанров'} id={'genre'}/>
}

function CheckboxesFormat({setParam}) {
  return <Checkboxes setParam={setParam} options={filterParams.format} label={'Формат выпуска'} placeholder={'Выберете один или несколько фрматов выпуска'} id={'format'}/>
}

function SelectList({label,list,title,setParam,value}){
  return(
    <FormControl fullWidth variant="outlined" margin='none'>
      <InputLabel htmlFor={`outlined-${label}-native-simple`}>{title}</InputLabel>
      <Select
        labelId={`simple-select-${label}-label`}
        id={`simple-${label}`}
        native
        value={value}
        onChange={e=>setParam(e.target.value)}
        label={label}
      > 
        {list.map(el=><option id={`select-${label}-${el.id}`} value={el.id}>{el.name}</option>)}
      </Select>
    </FormControl>
  )
}

function SelectTypeList({setParam,value}){
  return <SelectList value={value} setParam={setParam} label={'type'} list={filterParams.type} title={'Тип'} />
}
function SelectStatusList({setParam,value}){
  return <SelectList value={value} setParam={setParam} label={'status'} list={filterParams.status} title={'Статус'} />
}

function Checkboxes({options,label,placeholder,id,setParam}) {
  return (
    <Autocomplete
      multiple
      id={id}
      options={options}
      disableCloseOnSelect
      onChange={(e,v)=>{setParam(v.map(el=>el.id))}}
      getOptionLabel={(option) => option.name}
      renderOption={(option, { selected }) => (
        <React.Fragment>
          <Checkbox
            // icon={icon}
            // checkedIcon={checkedIcon}
            style={{ marginRight: 8 }}
            checked={selected}
          />
          {option.name}
        </React.Fragment>
      )}
      renderInput={(params) => (
        <TextField {...params} variant="outlined" label={label} placeholder={placeholder} />
      )}
    />
  );
}








